'use client';

import React, { useState, useEffect, useRef } from 'react';
import { Play, Pause, RotateCcw, Volume2 } from 'lucide-react';
import { formatDuration } from '../utils/audio';
import { useWaveform } from '../hooks/useWaveform';
import { WAVEFORM_CONFIG } from '../constants/audio';

interface AudioPlayerProps {
  audioUrl: string;
  title?: string;
  className?: string;
}

const AudioPlayer: React.FC<AudioPlayerProps> = ({ audioUrl, title, className = '' }) => {
  const [isPlaying, setIsPlaying] = useState(false);
  const [currentTime, setCurrentTime] = useState(0);
  const [duration, setDuration] = useState(0);
  const [volume, setVolume] = useState(1);
  const [showVolume, setShowVolume] = useState(false);
  
  const progressRef = useRef<HTMLDivElement>(null);
  const volumeRef = useRef<HTMLDivElement>(null);
  
  const { 
    containerRef, 
    loadAudio, 
    playPause, 
    stop, 
    seekTo, 
    getCurrentTime, 
    getDuration 
  } = useWaveform(WAVEFORM_CONFIG.DEFAULT);

  // Load audio when URL changes
  useEffect(() => {
    if (audioUrl) {
      loadAudio(audioUrl);
    }
  }, [audioUrl, loadAudio]);

  // Update time display
  useEffect(() => {
    const interval = setInterval(() => {
      if (isPlaying) {
        const time = getCurrentTime();
        const dur = getDuration();
        setCurrentTime(time);
        setDuration(dur);
      }
    }, 100);

    return () => clearInterval(interval);
  }, [isPlaying, getCurrentTime, getDuration]);

  const handlePlayPause = () => {
    playPause();
    setIsPlaying(!isPlaying);
  };

  const handleStop = () => {
    stop();
    setIsPlaying(false);
    setCurrentTime(0);
  };

  const handleProgressClick = (e: React.MouseEvent<HTMLDivElement>) => {
    if (!progressRef.current) return;
    
    const rect = progressRef.current.getBoundingClientRect();
    const clickX = e.clientX - rect.left;
    const progressWidth = rect.width;
    const clickPercent = clickX / progressWidth;
    
    const newTime = clickPercent * duration;
    seekTo(newTime);
    setCurrentTime(newTime);
  };

  const handleVolumeChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    const newVolume = parseFloat(e.target.value);
    setVolume(newVolume);
    // You can add volume control to WaveSurfer here if needed
  };

  const formatTime = (time: number) => {
    return formatDuration(time);
  };

  return (
    <div className={`audio-player ${className}`}>
      {title && (
        <h3 className="text-lg font-medium text-gray-900 mb-3">{title}</h3>
      )}
      
      {/* Waveform */}
      <div 
        ref={containerRef}
        className="waveform-container mb-4"
      />
      
      {/* Progress Bar */}
      <div className="mb-4">
        <div
          ref={progressRef}
          className="progress-bar w-full bg-gray-200 rounded-full h-2"
          onClick={handleProgressClick}
        >
          <div
            className="progress-fill bg-indigo-600 h-2 rounded-full transition-all duration-100 ease-out relative"
            style={{ width: `${duration > 0 ? (currentTime / duration) * 100 : 0}%` }}
          >
            <div className="progress-handle" />
          </div>
        </div>
        
        {/* Time Display */}
        <div className="flex justify-between text-sm text-gray-600 mt-2">
          <span>{formatTime(currentTime)}</span>
          <span>{formatTime(duration)}</span>
        </div>
      </div>
      
      {/* Controls */}
      <div className="audio-controls">
        <div className="flex items-center space-x-2">
          <button
            onClick={handlePlayPause}
            className="control-button primary w-10 h-10"
          >
            {isPlaying ? (
              <Pause className="h-5 w-5" />
            ) : (
              <Play className="h-5 w-5 ml-0.5" />
            )}
          </button>
          
          <button
            onClick={handleStop}
            className="control-button secondary w-8 h-8"
          >
            <RotateCcw className="h-4 w-4" />
          </button>
        </div>
        
        {/* Volume Control */}
        <div className="volume-control">
          <button
            onClick={() => setShowVolume(!showVolume)}
            className="control-button tertiary w-8 h-8"
          >
            <Volume2 className="h-4 w-4" />
          </button>
          
          {showVolume && (
            <div
              ref={volumeRef}
              className="volume-popup"
            >
              <div className="flex items-center space-x-2">
                <Volume2 className="h-4 w-4 text-gray-600" />
                <input
                  type="range"
                  min="0"
                  max="1"
                  step="0.1"
                  value={volume}
                  onChange={handleVolumeChange}
                  className="w-20 h-2 slider"
                />
                <span className="text-xs text-gray-600 w-8">
                  {Math.round(volume * 100)}%
                </span>
              </div>
            </div>
          )}
        </div>
      </div>
    </div>
  );
};

export default AudioPlayer; 