import { useState, useCallback } from 'react';
import { ProcessingStatus, AudioTrack } from '../types/audio';
import { AUDIO_CONFIG, MOCK_TRACKS } from '../constants/audio';

export const useAudioProcessing = () => {
  const [selectedFile, setSelectedFile] = useState<File | null>(null);
  const [processingStatus, setProcessingStatus] = useState<ProcessingStatus>('idle');
  const [progress, setProgress] = useState(0);
  const [currentStep, setCurrentStep] = useState('');
  const [error, setError] = useState<string | null>(null);
  const [separatedTracks, setSeparatedTracks] = useState<AudioTrack[]>([]);

  const handleFileSelect = useCallback((file: File) => {
    setSelectedFile(file);
    setProcessingStatus('idle');
    setError(null);
    setSeparatedTracks([]);
  }, []);

  const startProcessing = useCallback(async () => {
    if (!selectedFile) return;

    setProcessingStatus('uploading');
    setProgress(0);
    setError(null);

    try {
      // Simulate upload progress
      for (let i = 0; i <= 100; i += 10) {
        setProgress(i);
        await new Promise(resolve => setTimeout(resolve, 200));
      }

      setProcessingStatus('processing');
      setProgress(0);

      // Simulate processing steps
      for (let i = 0; i < AUDIO_CONFIG.PROCESSING_STEPS.length; i++) {
        setCurrentStep(AUDIO_CONFIG.PROCESSING_STEPS[i]);
        for (let j = 0; j <= 100; j += 20) {
          setProgress((i * 100 + j) / AUDIO_CONFIG.PROCESSING_STEPS.length);
          await new Promise(resolve => setTimeout(resolve, 300));
        }
      }

      // Set completed tracks
      setSeparatedTracks([...MOCK_TRACKS]);
      setProcessingStatus('completed');
      setProgress(100);

    } catch (err) {
      setError('Processing failed. Please try again.');
      setProcessingStatus('error');
    }
  }, [selectedFile]);

  const resetAll = useCallback(() => {
    setSelectedFile(null);
    setProcessingStatus('idle');
    setProgress(0);
    setCurrentStep('');
    setError(null);
    setSeparatedTracks([]);
  }, []);

  const handleDownload = useCallback((trackId: string) => {
    console.log(`Downloading track: ${trackId}`);
    // In production, this would trigger a real download
  }, []);

  return {
    selectedFile,
    processingStatus,
    progress,
    currentStep,
    error,
    separatedTracks,
    handleFileSelect,
    startProcessing,
    resetAll,
    handleDownload,
  };
}; 